import React, { useEffect, useState, useContext } from 'react';

import { Tabs, Tab } from 'react-bootstrap';
import Axios from 'axios';
import User from '../components/userDisplay';
import Page404 from './404';
import { AuthContext } from '../contexts/auth_context';

import { getApiLink } from '../lib/env.lib';
import ListLayout from '../components/cardListLayoutLoader';

function ControlledTabs(props) {
  const [key, setKey] = useState('article');

  return (
    <>
      <div className="tab-section">
        <Tabs
          variant="tabs"
          id="controlled-tab-example"
          activeKey={key}
          transition={false}
          onSelect={(k) => setKey(k)}
        >
          <Tab
            eventKey="article"
            title={
              props.mode === 'self' ? 'Your\xa0Articles' : 'Users\xa0Articles'
            }
          >
            {/* {articles} */}
            <ListLayout
              link={`/api/v1/getArticlesByUsername/${props.username}`}
            />
          </Tab>
          {/* <Tab eventKey="profile" title="Profile">
                        2
                                </Tab>
                    <Tab eventKey="contact" title="Contact">
                        3</Tab>

                    <Tab eventKey="contacts" title="third">
                        4</Tab> */}
        </Tabs>
      </div>
    </>
  );
}

function Dashboard(props) {
  const {
    user_id,
    user_dp,
    user_fullname,
    user_username,
    user_bio,
  } = useContext(AuthContext);

  const [username, setUsername] = useState('');
  const [id, setId] = useState('');
  const [dp, setDp] = useState('');
  const [name, setName] = useState('');
  const [bio, setBio] = useState('');

  const [loaded, setLoaded] = useState(0);
  useEffect(() => {
    if (props.mode === 'self') {
      setUsername(user_username);
      setId(user_id);
      setDp(user_dp);
      setName(user_fullname);
      setBio(user_bio);
      setLoaded(1);
    } else {
      setUsername(props.username);

      Axios.get(`${getApiLink()}/api/v1/getUserByUsername/${props.username}`)
        .then((response) => {
          if (response.data.success) {
            setId(response.data.message.data.user_id);
            setDp(response.data.message.data.details.profile_image_link);
            setName(response.data.message.data.details.fullname);
            setBio(response.data.message.data.details.user_bio);
            setLoaded(1);
          } else {
            setLoaded(2);
          }
        })
        .catch((e) => {
          console.log(e);
          if (e.response.data.error.message !== null) {
            console.log(`User loading: ${e.response.data.error.message}`);
          }

          setLoaded(2);
        });
    }
  }, []);
  return (
    <>
      {(() => {
        if (loaded === 1) {
          return (
            <>
              <div className="container-xl">
                <div className="row">
                  <div className="col-lg-2" />
                  <div className="col-lg-8">
                    <div className="container-lg">
                      <User
                        mode={username === user_username ? 'self' : props.mode}
                        username={username}
                        name={name}
                        dp={dp}
                        user_id={id}
                        bio={bio}
                      />

                      <ControlledTabs
                        data={props}
                        username={username}
                        mode={username === user_username ? 'self' : props.mode}
                      />
                    </div>
                  </div>
                  <div className="col-lg-2" />
                </div>
              </div>
            </>
          );
        }
        if (loaded === 2) {
          return (
            <>
              {' '}
              <Page404 />
            </>
          );
        }
      })()}
    </>
  );
}

export default Dashboard;
